async function loadEmailData() {
  const item = Office.context.mailbox.item;
  if (!item) return;

  try {
    if (item.sender) {
      let senderEmail = item.sender.emailAddress;
      let senderName = item.sender.displayName;
  
      senderEmail = cleanEmail(senderEmail);
      senderName = senderName || senderEmail.split('@')[0];
  
      // Pré-remplir le champ nom et email dans la page de recherche
      const searchNameInput = document.getElementById('search-name');
      const searchEmailInput = document.getElementById('search-sender-email');
      const nameRadio = document.getElementById('search-name-radio');
      
      if (searchNameInput) {
          searchNameInput.value = senderName;
          searchNameInput.disabled = false; // Activer le champ nom
      }
      if (searchEmailInput) {
          searchEmailInput.value = senderEmail;
          searchEmailInput.disabled = true; // Désactiver le champ email initialement
      }
      if (nameRadio) {
          nameRadio.checked = true; // S'assurer que le radio nom est coché
      }

      // Activer le bouton de recherche initialement
      const searchBtn = document.getElementById('search-contact-btn');
      if (searchBtn) {
          searchBtn.disabled = false;
      }
    }

    // Chargement des pièces jointes
    if (item.attachments) {
        window.attachmentsToUpload = await Promise.all(
            item.attachments
                .filter(a => !a.isInline)
                .map(async (attachment) => {
                    const content = await getAttachmentContent(attachment.id);
                    return {
                        name: attachment.name,
                        datas: content,
                        mimetype: attachment.contentType || 'application/octet-stream',
                        type: 'binary'
                    };
                })
        );
    }

    const result = await new Promise((resolve, reject) => {
        item.body.getAsync(Office.CoercionType.Text, (result) => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                resolve(result.value);
            } else {
                reject(new Error('Échec chargement message'));
            }
        });
    });

    // Extraction et configuration des numéros de téléphone
    const phones = extractPhoneNumbers(result);
    const phoneInput = document.getElementById('search-phone-number');
    const phoneSelect = document.getElementById('search-phone-select');
    
    if (phoneInput) {
        phoneInput.disabled = true; // Désactiver le champ téléphone initialement
    }
    if (phoneSelect) {
        phoneSelect.disabled = true; // Désactiver le select téléphone initialement
    }
    
    populatePhoneSelect(phones);

  } catch (error) {
      console.error('Erreur chargement données:', error);
      showNotification('Erreur lors du chargement des données', 'error');
      throw error;
  }
}

function cleanEmail(email) {
  return email.replace(/<mailto:|>/g, '').trim().split(' ')[0];
}

function extractPhoneNumbers(text) {
  function countDigits(str) {
      return (str.match(/\d/g) || []).length;
  }

  const phonePatterns = [
      // Format international avec + ou 00
      /(?:\+|00)[1-9]\d{1,3}[- .]?\d(?:[- .]?\d){4,14}/g,
      // Format belge spécifique avec parenthèses et /
      /\((?:\+|00)?32\)[-\s]?[0-9][0-9\/\s.-]{7,14}/g,
      // Format avec parenthèses génériques
      /\([0-9]{2,4}\)[-\s]?[0-9][0-9\/\s.-]{7,14}/g,
      // Format belge sans parenthèses
      /(?:0|\+32|0032)[- .]?(?:4[0-9]{2}|[1-9][0-9])[- .]?(?:[0-9]{2}[- .]?){3}/g,
      // Formats standard avec séparateurs variés
      /\d+(?:[-\s.\/]+\d+){4,}/g
  ];

  function formatPhoneNumber(phone) {
      if (countDigits(phone) < 6) {
          return null;
      }

      // Nettoyer le numéro
      let cleaned = phone.trim()
          .replace(/[\(\)]/g, '') // Enlever les parenthèses
          .replace(/^0{2}/, '+')  // Remplacer 00 initial par +
          .replace(/^0(?=32)/, '+') // Remplacer 0 initial par + pour format belge
          .replace(/[\/\s.-]+/g, ' ') // Normaliser les séparateurs en espaces
          .replace(/\s+/g, ' ')  // Normaliser les espaces multiples
          .trim();

      // Gérer spécifiquement les numéros belges
      if (cleaned.match(/^\+32|^0/)) {
          cleaned = cleaned
              .replace(/^0/, '+32') // Convertir 0 initial en +32
              .replace(/(\+32)0/, '$1'); // Enlever le 0 après +32 s'il existe
      }

      return {
          formatted: cleaned,
          digitCount: countDigits(cleaned)
      };
  }

  let phones = new Map();

  for (let pattern of phonePatterns) {
      const matches = text.matchAll(pattern);
      for (const match of matches) {
          const phoneInfo = formatPhoneNumber(match[0]);
          if (phoneInfo && phoneInfo.digitCount >= 6) {
              if (!phones.has(phoneInfo.formatted)) {
                  phones.set(phoneInfo.formatted, phoneInfo);
              }
          }
      }
  }

  return Array.from(phones.values());
}

function populatePhoneSelect(phones) {
  const phoneSelect = document.getElementById('search-phone-select');
  const phoneInput = document.getElementById('search-phone-number');
  
  while (phoneSelect.options.length > 1) {
      phoneSelect.remove(1);
  }

  phones.forEach((phoneInfo, index) => {
      const option = document.createElement('option');
      option.value = phoneInfo.formatted;
      option.textContent = `Numéro ${index + 1}: ${phoneInfo.formatted}`;
      phoneSelect.appendChild(option);
  });

  if (phones.length > 0) {
      phoneInput.value = phones[0].formatted;
  }
}

function getAttachmentContent(attachmentId) {
  return new Promise((resolve, reject) => {
      Office.context.mailbox.item.getAttachmentContentAsync(attachmentId, (result) => {
          if (result.status === Office.AsyncResultStatus.Succeeded) {
              resolve(result.value.content);
          } else {
              reject(new Error('Échec récupération pièce jointe'));
          }
      });
  });
}

function setupEventListeners() {
  // Gestion des radio buttons
  const radioButtons = document.querySelectorAll('input[name="search-type"]');
  const searchBtn = document.getElementById('search-contact-btn');

  // S'assurer que les éléments existent
  if (!searchBtn) {
      console.error('Bouton de recherche non trouvé');
      return;
  }

  radioButtons.forEach(radio => {
      radio.addEventListener('change', function() {
          // Désactiver tous les champs
          document.getElementById('search-name').disabled = true;
          document.getElementById('search-sender-email').disabled = true;
          document.getElementById('search-phone-number').disabled = true;
          document.getElementById('search-phone-select').disabled = true;

          // Activer le champ correspondant au radio sélectionné
          switch(this.value) {
              case 'name':
                  document.getElementById('search-name').disabled = false;
                  break;
              case 'email':
                  document.getElementById('search-sender-email').disabled = false;
                  break;
              case 'phone':
                  document.getElementById('search-phone-number').disabled = false;
                  document.getElementById('search-phone-select').disabled = false;
                  break;
          }
      });
  });

  // Gestion du select de téléphone
  const phoneSelect = document.getElementById('search-phone-select');
  if (phoneSelect) {
      phoneSelect.addEventListener('change', (e) => {
          const phoneInput = document.getElementById('search-phone-number');
          if (phoneInput && e.target.value) {
              phoneInput.value = e.target.value;
          }
      });
  }

  if (searchBtn) {
      searchBtn.addEventListener('click', handleSearch);
  }
}



async function handleSearch() {
  const button = document.getElementById('search-contact-btn');
  const resultsContainer = document.getElementById('search-results');
  
  try {
      button.disabled = true;
      button.innerHTML = '<i class="ms-Icon ms-Icon--Search"></i>Recherche en cours...';

      const criteria = buildSearchCriteria();
      if (!criteria) {
          showNotification('Veuillez sélectionner au moins un critère de recherche', 'error');
          return;
      }

      const odoo = new OdooAPI();
      const partners = await odoo.searchPartners(criteria);
      displayResults(partners, odoo.host);

  } catch (error) {
      console.error('Erreur recherche:', error);
      showNotification(error.message, 'error');
  } finally {
      button.disabled = false;
      button.innerHTML = '<i class="ms-Icon ms-Icon--Search"></i>Chercher le contact';
  }
}

function buildSearchCriteria() {
  const selectedType = document.querySelector('input[name="search-type"]:checked');
  if (!selectedType) return null;

  const criteria = {};
  
  // Correction de la logique pour obtenir l'ID du champ
  let fieldId;
  switch(selectedType.value) {
      case 'phone':
          fieldId = 'search-phone-number';
          break;
      case 'email':
          fieldId = 'search-sender-email';  // ID correct pour l'email
          break;
      case 'name':
          fieldId = 'search-name';
          break;
      default:
          return null;
  }

  const inputField = document.getElementById(fieldId);
  if (!inputField) {
      console.error(`Champ ${fieldId} non trouvé`);
      return null;
  }

  const value = inputField.value.trim();
  
  if (value) {
      if (selectedType.value === 'phone') {
          criteria.phone = value.replace(/[^0-9+]/g, '');
      } else {
          criteria[selectedType.value] = value;
      }
  }

  return Object.keys(criteria).length > 0 ? criteria : null;
}
function displayResults(partners, odooHost) {
  const container = document.getElementById('search-results');
  container.innerHTML = '';

  if (partners.length === 0) {
      container.innerHTML = '<div class="no-results">Aucun contact trouvé</div>';
      return;
  }

  const cardsContainer = document.createElement('div');
  cardsContainer.className = 'partner-cards';

  partners.forEach(partner => {
      const card = createPartnerCard(partner, odooHost);
      cardsContainer.appendChild(card);
  });

  container.appendChild(cardsContainer);
  setupCardListeners();
}

function createPartnerCard(partner, odooHost) {
  const card = document.createElement('div');
  card.className = 'partner-card';
  card.dataset.partnerId = partner.id;
  card.innerHTML = `
      <div class="card-header">
          <input type="radio" name="partner-select" class="partner-select" data-partner-id="${partner.id}">
          <h3 class="partner-name">${partner.name}</h3>
      </div>
      <div class="card-content">
          <p class="partner-street">${partner.street || ''}</p>
          <p class="partner-city">${partner.zip || ''} ${partner.city || ''}</p>
      </div>
      <div class="card-footer">
          <a href="${odooHost}/web#id=${partner.id}&model=res.partner&view_type=form" 
             target="_blank" class="view-button">
             Voir dans Odoo
          </a>
          <button class="upload-button" style="display: none;" data-partner-id="${partner.id}">
              <i class="ms-Icon ms-Icon--Upload"></i> Upload sélection
          </button>
      </div>
      <div class="attachment-section" style="display: none;">
          <h4>Pièces jointes disponibles</h4>
          <div class="attachment-list"></div>
      </div>
  `;
  return card;
}

async function loadAttachments(attachmentList) {
  attachmentList.innerHTML = '';
  console.log('Début du chargement des pièces jointes');
  
  const item = Office.context.mailbox.item;
  
  // Réinitialiser le tableau des pièces jointes
  window.attachmentsToUpload = [];
  let attachmentIndex = 0;

  // Charger toutes les pièces jointes, y compris les inline
  if (item.attachments) {
      // Filtrer et traiter les pièces jointes
      const attachments = item.attachments.filter(attachment => {
          // Garder les pièces jointes normales et les images inline
          return !attachment.isInline || 
                 (attachment.contentType && attachment.contentType.startsWith('image/'));
      });

      // Charger le contenu de chaque pièce jointe
      for (const attachment of attachments) {
          try {
              const content = await getAttachmentContent(attachment.id);
              let attachmentItem;

              if (attachment.isInline && attachment.contentType.startsWith('image/')) {
                  // Créer un item pour une image inline
                  attachmentItem = createImageAttachmentItem(
                      `data:${attachment.contentType};base64,${content}`,
                      attachmentIndex,
                      attachment.name
                  );
                  
                  // Stocker l'image inline
                  window.attachmentsToUpload[attachmentIndex] = {
                      name: attachment.name,
                      datas: content,
                      mimetype: attachment.contentType,
                      type: 'binary',
                      isInline: true
                  };
              } else {
                  // Créer un item pour une pièce jointe normale
                  const attachmentData = {
                      name: attachment.name,
                      datas: content,
                      mimetype: attachment.contentType || 'application/octet-stream',
                      type: 'binary'
                  };
                  attachmentItem = createAttachmentItem(attachmentData, attachmentIndex);
                  
                  // Stocker la pièce jointe normale
                  window.attachmentsToUpload[attachmentIndex] = attachmentData;
              }

              attachmentList.appendChild(attachmentItem);
              attachmentIndex++;

          } catch (error) {
              console.error(`Erreur chargement pièce jointe ${attachment.name}:`, error);
          }
      }
  }

  // Essayer de récupérer les images du corps du mail
  try {
      const body = await new Promise((resolve, reject) => {
          item.body.getAsync(
              Office.CoercionType.Html,
              { asyncContext: "This is passed to the callback" },
              function (asyncResult) {
                  if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
                      resolve(asyncResult.value);
                  } else {
                      reject(new Error('Échec chargement HTML'));
                  }
              }
          );
      });

      const tempDiv = document.createElement('div');
      tempDiv.innerHTML = body;

      // Chercher les images qui ne sont pas des CID
      const images = Array.from(tempDiv.getElementsByTagName('img'))
          .filter(img => {
              const src = img.src || img.getAttribute('src');
              return src && 
                     !src.startsWith('cid:') && 
                     !src.startsWith('data:') && 
                     !src.includes('outlook.office365.com') && 
                     !src.includes('outlook.office.com');
          });

      // Ajouter les images trouvées
      for (const img of images) {
          const src = img.src || img.getAttribute('src');
          if (src) {
              const item = createImageAttachmentItem(
                  src, 
                  attachmentIndex,
                  `Image ${attachmentIndex + 1}`
              );
              attachmentList.appendChild(item);
              
              // Stocker l'URL de l'image pour l'upload ultérieur
              window.attachmentsToUpload[attachmentIndex] = {
                  name: `Image ${attachmentIndex + 1}.png`,
                  src: src,
                  type: 'image',
                  mimetype: 'image/png'
              };
              
              attachmentIndex++;
          }
      }

  } catch (error) {
      console.error('Erreur chargement images:', error);
  }
}

// Modifier la fonction createImageAttachmentItem pour accepter un nom
function createImageAttachmentItem(src, index, name = null) {
  const div = document.createElement('div');
  div.className = 'attachment-item';
  
  const displayName = name || `Image ${index + 1}`;
  
  div.innerHTML = `
      <input type="checkbox" id="image-${index}" data-type="image" data-src="${src}">
      <img src="${src}" class="image-preview" alt="Preview" onerror="this.style.display='none'">
      <label for="image-${index}">
          ${displayName}
          <br>
          <small class="image-source">${src.startsWith('data:') ? 'Image intégrée' : src.substring(0, 50) + '...'}</small>
      </label>
  `;

  return div;
}

function createAttachmentItem(attachment, index) {
  const div = document.createElement('div');
  div.className = 'attachment-item';
  
  // Ajouter des styles spécifiques pour les pièces jointes
  const styles = document.createElement('style');
  styles.textContent = `
      .attachment-item {
          display: flex;
          align-items: center;
          padding: 10px;
          border: 1px solid #ddd;
          margin-bottom: 5px;
          border-radius: 4px;
      }
      .attachment-item:hover {
          background-color: #f5f5f5;
      }
      .file-type {
          color: #666;
          font-size: 0.8em;
          margin-left: 5px;
      }
      .attachment-item label {
          cursor: pointer;
          flex: 1;
      }
      .attachment-item input[type="checkbox"] {
          margin-right: 10px;
      }
  `;
  document.head.appendChild(styles);

  div.innerHTML = `
      <input type="checkbox" id="attachment-${index}" data-type="file">
      <label for="attachment-${index}">
          ${attachment.name}
          <span class="file-type">(${attachment.mimetype})</span>
      </label>
  `;

  return div;
}




function setupCardListeners() {
    // Gestion des radios
    document.querySelectorAll('.partner-select').forEach(radio => {
        radio.addEventListener('change', async function() {
            const selectedCard = this.closest('.partner-card');
            const partnerId = this.dataset.partnerId;
            
            if (this.checked) {
                // Si la carte est sélectionnée
                // Cacher les autres cartes
                document.querySelectorAll('.partner-card').forEach(card => {
                    if (card !== selectedCard) {
                        card.style.display = 'none';
                    }
                });

                // Afficher la section des pièces jointes
                const attachmentSection = selectedCard.querySelector('.attachment-section');
                const attachmentList = selectedCard.querySelector('.attachment-list');
                const uploadButton = selectedCard.querySelector('.upload-button');
                
                attachmentSection.style.display = 'block';
                uploadButton.style.display = 'block';
                
                // Charger les pièces jointes
                await loadAttachments(attachmentList);
            } else {
                // Si la carte est désélectionnée
                // Réafficher toutes les cartes
                document.querySelectorAll('.partner-card').forEach(card => {
                    card.style.display = 'block';
                });

                // Cacher la section des pièces jointes et le bouton d'upload
                const attachmentSection = selectedCard.querySelector('.attachment-section');
                const uploadButton = selectedCard.querySelector('.upload-button');
                
                attachmentSection.style.display = 'none';
                uploadButton.style.display = 'none';
            }
        });

        // Permettre la désélection en cliquant à nouveau sur le radio
        radio.addEventListener('click', function(e) {
            if (this.checked && this.dataset.wasChecked === 'true') {
                this.checked = false;
                this.dispatchEvent(new Event('change'));
            }
            this.dataset.wasChecked = this.checked;
        });
    });

    // Gestion des boutons d'upload
    document.querySelectorAll('.upload-button').forEach(button => {
        button.addEventListener('click', handleUpload);
    });
}

async function handleUpload() {
  const partnerId = this.dataset.partnerId;
  const originalText = this.innerHTML;
  const card = this.closest('.partner-card');
  
  try {
      this.disabled = true;
      this.innerHTML = '<i class="ms-Icon ms-Icon--Sync"></i> Upload en cours...';
      
      const selectedAttachments = card.querySelectorAll('.attachment-item input[type="checkbox"]:checked');
      
      if (selectedAttachments.length === 0) {
          showNotification('Veuillez sélectionner au moins une pièce jointe', 'error');
          return;
      }
      
      const odoo = new OdooAPI();
      
      for (const checkbox of selectedAttachments) {
          const id = parseInt(checkbox.id.split('-')[1]);
          const storedAttachment = window.attachmentsToUpload[id];
          
          if (!storedAttachment) {
              console.error(`Pièce jointe non trouvée pour l'index ${id}`);
              continue;
          }

          let attachmentData;
          
          if (storedAttachment.type === 'image' && storedAttachment.src) {
              // Image du corps du mail
              const datas = await fetchImageAsBase64(storedAttachment.src);
              attachmentData = {
                  name: storedAttachment.name,
                  datas: datas,
                  mimetype: storedAttachment.mimetype,
                  type: 'binary',
                  res_model: 'res.partner',
                  res_id: parseInt(partnerId)
              };
          } else {
              // Pièce jointe normale ou image inline
              attachmentData = {
                  ...storedAttachment,
                  res_model: 'res.partner',
                  res_id: parseInt(partnerId)
              };
          }
          
          await odoo.createAttachment(attachmentData);
      }
      
      showNotification('Pièces jointes uploadées avec succès', 'success');
      
      // Reset
      card.querySelector('.partner-select').checked = false;
      card.querySelector('.attachment-section').style.display = 'none';
      this.style.display = 'none';
      document.querySelectorAll('.partner-card').forEach(c => {
          c.style.display = 'block';
      });
      
  } catch (error) {
      console.error('Erreur upload:', error);
      showNotification('Erreur lors de l\'upload des pièces jointes', 'error');
  } finally {
      this.disabled = false;
      this.innerHTML = originalText;
  }
}

// Fonction utilitaire pour convertir une image en base64
async function fetchImageAsBase64(url) {
  const response = await fetch(url);
  const blob = await response.blob();
  return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => resolve(reader.result.split(',')[1]);
      reader.onerror = reject;
      reader.readAsDataURL(blob);
  });
}